export default {
	props: {
		value: [Number, String],
		type: { // 传值
			type: Array,
			default: ()=> {
				return []
			}
		},
		itemColor: { // 主色调
			type: String,
			default: '#01cfc5'
		},
		lineColor: { // 主色调
			type: String,
			default: '#01cfc5'
		},
		duration: {
			type: Number,
			default: 0.3
		},
	},
	data() {
		return {
			currentIndex: 0,
			lineStyle: {},
			scrollLeft: 0
		}
	},
	computed: {
		scrollable() {
			return (this.type && this.type.length > 4)
		}
	},
	watch: {
		value() {
			this.currentIndex = this.value
		}
	},
	mounted() {
		this.setLine()
		this.scrollIntoView()
	},
	methods: {
		select(item, index) {
			this.currentIndex = index
			this.setLine()
			this.scrollIntoView()
			this.$emit('input', this.currentIndex)
		},
		setLine() {
			let lineWidth = 0, lineLeft = 0
			this.getElementData(`#tab_${this.currentIndex}`, (data)=> {
				console.log(data);
				let el = data[0]
				lineWidth = el.width / 2
				lineLeft = el.width * (this.currentIndex + 0.5)
				this.lineStyle = {
					width: `${lineWidth * 2}rpx`, // 1px -> 2rpx
					transform: `translateX(${lineLeft * 2}rpx) translateX(-50%)`,
					transitionDuration: `${this.duration}s`
				};
			})
		},
		scrollIntoView() {  // item滚动
			let count = 0;
			let frames = 30;
			let lineLeft = 0;
			let self = this
			
			this.getElementData('#tab_list', (data)=> {
				let list = data[0]
				this.getElementData(`#tab_${this.currentIndex}`, (data)=> {
					let el = data[0]
					lineLeft = el.width * (this.currentIndex + 0.5) - list.width / 2 - this.scrollLeft
					animate();
				})
			})
			
			function animate() {
				self.scrollLeft += lineLeft / frames;
			    if (++count < frames) {
					setTimeout(animate, 10);
				}
			}
		},
		getElementData(el, callback){
			uni.createSelectorQuery().in(this).select(el).boundingClientRect().exec((data) => {
				callback(data);
			}); 
		}
	}
}
